import React, { Component } from 'react'

export class Search extends Component {
  state = {
    input: '' //batman begins
  }

  handleClick = () => {
    const dataDisubmit = this.state.input.replace(' ', '+')
    this.props.getMovies(dataDisubmit)
  }

  handleSubmit = e => {
    e.preventDefault()
    const dataDisubmit = this.state.input.replace(' ', '+')
    this.props.getMovies(dataDisubmit)
  }

  handleChange = e => {
    console.log(e.target.value)
    this.setState({ input: e.target.value })
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit} className='form-inline my-2 my-lg-5'>
        <input
          onChange={this.handleChange}
          className='form-control mr-sm-2'
          type='search'
          placeholder='Search'
          aria-label='Search'
        />
        <button className='btn btn-outline-success my-2 my-sm-0' type='submit'>
          Search
        </button>
      </form>
    )
  }
}

export default Search
